import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import appConfig from "./config.json";

function IconLink(props) {
  return (
    <a
      href={props.href}
      className={"card-link" + props.wrapperClass}
      rel="noreferrer"
      target="_blank"
    >
      <FontAwesomeIcon icon={props.icon} size="lg" />
    </a>
  );
}

function Footer() {
  const notAuthenticated = !localStorage.getItem(appConfig.KEY_TOKEN);

  let wrapperClass = "footer py-4";
  let linkClass = "";
  if (notAuthenticated) {
    wrapperClass += " bg-light";
    linkClass += " dark-link";
  } else {
    wrapperClass += " bg-primary text-white";
    linkClass += " white-link";
  }

  return (
    <footer>
      <div className={wrapperClass}>
        <div className="container">
          <div className="d-none d-sm-block">
            <div className="d-flex align-items-center justify-content-between">
              <div className="small font-weight-medium">
                Copyright © VStore 2020
              </div>
              <div>
                <IconLink
                  href="https://www.facebook.com/vstoremyanmar/"
                  icon={["fab", "facebook-square"]}
                  wrapperClass={linkClass}
                />
                <IconLink
                  href="https://m.me/vstoremyanmar/"
                  icon={["fab", "facebook-messenger"]}
                  wrapperClass={linkClass}
                />
              </div>
            </div>
          </div>
          <div className="d-block d-sm-none">
            <div className="row mb-2">
              <div className="mx-auto small">
                <div>Copyright © VStore 2020</div>
              </div>
            </div>
            <div className="row">
              <div className="mx-auto">
                <IconLink
                  href="https://www.facebook.com/vstoremyanmar/"
                  icon={["fab", "facebook-square"]}
                  wrapperClass={linkClass}
                />
                <IconLink
                  href="https://m.me/vstoremyanmar/"
                  icon={["fab", "facebook-messenger"]}
                  wrapperClass={linkClass}
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    </footer>
  );
}

export default Footer;
