import { useEffect, useState } from "react";
import axios from "axios";
import { syncData } from "./ProductAPI";
import appConfig from "../common/config.json";
import GridCell from "./GridCell";
import ProductCell from "./ProductCell";
import Loading from "../common/Loading";
import ErrorMessage from "../common/ErrorMessage";
import { Redirect } from "react-router-dom";
import PurchaseEPin from "./PurchaseEPin";
import PurchaseTopup from "./PurchaseTopup";
import Profile from "../profile/Profile";
import PurchaseResult from "./PurchaseResult";

function Product(props) {
  const [items, setItems] = useState([]);
  const [history, setHistory] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState("");

  const details = ["purchase", "result"];

  function getKey() {
    return history.length > 0
      ? history[history.length - 1].key
      : appConfig.KEY_CACHE_STORES;
  }

  useEffect(() => {
    document.title = "VStore | Digital Store";
    const source = axios.CancelToken.source();
    const expiry = localStorage.getItem(appConfig.KEY_CACHE_EXPIRY);
    const key = getKey();

    if (details.includes(key)) {
    } else {
      let rawJson = localStorage.getItem(key);

      if (!rawJson || Number(expiry) <= new Date().getTime()) {
        setItems([]);
        setLoading(true);
        setError("");
        syncData(source.token)
          .then((_) => {
            setLoading(false);
            updateListing(localStorage.getItem(key));
          })
          .catch((error) => {
            if (axios.isCancel(error)) {
              console.log("Request cancelled.");
            } else {
              let msg = "Something went wrong.";
              console.log(error);
              if (error.response) {
                let data = error.response.data;
                msg = data.errorCode ? data.errorCode : data;
              }

              setLoading(false);
              setError(msg);
            }
          });
      } else {
        updateListing(rawJson);
      }
    }

    return () => {
      if (source != null) {
        source.cancel();
      }
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [history]);

  function updateListing(rawJson) {
    let items = JSON.parse(rawJson);

    let parentId =
      history.length > 0 ? history[history.length - 1].item.id : "";

    items.sort((a, b) => a.orderIndex - b.orderIndex);
    setItems(!parentId ? items : items.filter((d) => d.parentId === parentId));
  }

  function navigate(evt, item) {
    evt.preventDefault();
    let _history = [...history];
    let key =
      _history.length > 0
        ? _history[_history.length - 1].key
        : appConfig.KEY_CACHE_STORES;
    let h = {
      item: item,
      step: _history.length,
    };

    switch (key) {
      case appConfig.KEY_CACHE_STORES:
        h["key"] = appConfig.KEY_CACHE_CATALOGS;
        break;
      case appConfig.KEY_CACHE_CATALOGS:
        h["key"] = appConfig.KEY_CACHE_PRODUCTS;
        break;
      case appConfig.KEY_CACHE_PRODUCTS:
        h["key"] = "purchase";
        break;
      default:
        return;
    }

    _history.push(h);
    setHistory(_history);
  }

  function jumpTo(evt, h) {
    evt.preventDefault();
    if (!h) {
      setHistory([]);
    } else {
      let _history = history.slice(0, h.step + 1);
      setHistory(_history);
    }
  }

  function handleSuccess(item, result) {
    let _history = [...history];
    let h = {
      item: item,
      result: result,
      step: _history.length,
      key: "result",
    };

    _history.push(h);
    setHistory(_history);
  }

  function getBreadcurmb() {
    if (history.length === 1) {
      return (
        <li className="breadcrumb-item active">{history[0].item.title}</li>
      );
    }

    let last = history[history.length - 1];
    return (
      <>
        {history
          .slice(0, history.length - 1)
          .filter((h) => !details.includes(h.key))
          .map((h, index) => {
            return (
              <li key={index} className="breadcrumb-item">
                <a
                  href="!#"
                  className="card-link"
                  onClick={(evt) => jumpTo(evt, h)}
                >
                  {h.item.title}
                </a>
              </li>
            );
          })}
        {!details.includes(last.key) && (
          <li className="breadcrumb-item active">{last.item.title}</li>
        )}
      </>
    );
  }

  function getBody() {
    let key = getKey();
    if ("purchase" === key) {
      let data = history[history.length - 1].item;
      switch (data.productType) {
        case "EPIN":
          return <PurchaseEPin data={data} onSuccess={handleSuccess} />;
        case "TELECOM_TOPUP":
          return <PurchaseTopup data={data} onSuccess={handleSuccess} />;

        default:
          return <Redirect to="/" />;
      }
    } else if ("result" === key) {
      let h = history[history.length - 1];
      return <PurchaseResult data={h.item} result={h.result} />;
    }

    return (
      <div className="col-md-12">
        <div className="row">
          {items.map((s, index) => {
            if ("sellingPrice" in s) {
              return (
                <div key={s.id} className="col-6 col-sm-6 col-md-6 mb-3">
                  <ProductCell
                    id={s.id}
                    logo={s.photoUrl}
                    title={s.title}
                    index={index}
                    price={s.sellingPrice}
                    onClick={(evt) => navigate(evt, s)}
                  />
                </div>
              );
            }
            return (
              <div key={s.id} className="col-6 col-sm-6 col-md-6 mb-3">
                <GridCell
                  id={s.id}
                  logo={s.photoUrl}
                  title={s.title}
                  index={index}
                  slug={s.id}
                  onClick={(evt) => navigate(evt, s)}
                />
              </div>
            );
          })}
        </div>
      </div>
    );
  }

  return (
    <>
      <div className="col-lg-4 d-none d-md-none d-lg-block">
        <Profile />
      </div>
      <div className="col-md-12 col-lg-8">
        {history.length > 0 && (
          <div className="row">
            <div className="col-md-12">
              <nav aria-label="breadcrumb">
                <ol className="breadcrumb bg-white shadow-cm">
                  <li className="breadcrumb-item">
                    <a
                      href="!#"
                      className="card-link"
                      onClick={(evt) => jumpTo(evt, null)}
                    >
                      Home
                    </a>
                  </li>

                  {getBreadcurmb()}
                </ol>
              </nav>
            </div>
          </div>
        )}

        <Loading show={loading} />
        <ErrorMessage message={error} />

        {getBody()}
      </div>
    </>
  );
}

export default Product;
