import axios from "axios";
import config from "../common/config.json";

export function getProfile(cancelToken) {
  const url = config.ROOT_PATH + "/private/get-my-profile";
  return axios.get(url, { cancelToken: cancelToken });
}

export function getBalance(cancelToken) {
  const url = config.ROOT_PATH + "/private/get-balance";
  return axios.get(url, { cancelToken: cancelToken });
}

export function getUnreadCount() {
  const url = config.ROOT_PATH + "/private/count-unread-notifications";
  return axios.get(url);
}

export function changeName(name, cancelToken) {
  const url = config.ROOT_PATH + "/private/change-name";
  return axios.post(url, { newName: name }, { cancelToken: cancelToken });
}

export function changePassword(body, cancelToken) {
  const url = config.ROOT_PATH + "/private/change-password";
  return axios.post(url, body, { cancelToken: cancelToken });
}
