import { useEffect, useRef, useState } from "react";
import TextArea from "../common/TextArea";
import ProductDetailHeader from "./ProductDetailHeader";
import axios from "axios";
import { purchaseEpin } from "./ProductAPI";
import ErrorMessage from "../common/ErrorMessage";

function PurchaseEPin(props) {
  const [form, setForm] = useState({
    remark: "",
  });
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState("");
  const source = useRef(null);

  useEffect(() => {
    return () => {
      if (source.current != null) source.current.cancel();
    };
  }, []);

  function getSource() {
    if (source.current == null) {
      const CancelToken = axios.CancelToken;
      source.current = CancelToken.source();
    }
    return source.current;
  }

  function handleChange({ target }) {
    setForm({
      ...form,
      [target.name]: target.value,
    });
  }

  function handleSubmit(event) {
    event.preventDefault();
    let body = {
      listingId: props.data.id,
      remark: form.remark,
    };
    setError("");
    setLoading(true);

    props.onSuccess(props.data, {
      expiryDate: "",
      status: "PROCESSING",
      serialKey: "11223344556677",
      purchaseId: "98706754312897765",
    });

    // purchaseEpin(body, getSource().token)
    //   .then((resp) => {})
    //   .catch((error) => {
    //     let msg = "Something went wrong.";
    //     console.log(error);
    //     if (error.response) {
    //       let data = error.response.data;
    //       msg = data.errorCode ? data.errorCode : data;
    //     }
    //     setLoading(false);
    //     setError(msg);
    //   });
  }

  return (
    <>
      <ErrorMessage message={error} />
      <ProductDetailHeader
        logo={props.data.photoUrl}
        title={props.data.title}
        amount={props.data.sellingPrice}
      />
      <div className="bg-white border-0 rounded-lg shadow-cm mb-3 col-md-12 px-4 py-3">
        <form onSubmit={handleSubmit}>
          <TextArea
            label="Remark"
            name="remark"
            type="text"
            onChange={handleChange}
            placeholder=""
            value={form.remark}
          />
          <div className="clearfix">
            <button
              type="submit"
              className="btn btn-success float-right"
              disabled={loading}
            >
              {loading && (
                <>
                  <span
                    className="spinner-border spinner-border-sm"
                    role="status"
                    aria-hidden="true"
                  ></span>
                  &nbsp;
                </>
              )}
              Purchase
            </button>
          </div>
        </form>
      </div>
    </>
  );
}

export default PurchaseEPin;
